<template>
  <div class="page">
    <div class="submissionDiv">
      Submit

      <v-file-input label="File input" @change="selectFile"></v-file-input>

      <v-btn @click="submit">Submit</v-btn>

    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'SubmissionPage',
  props: {
    msg: String
  },
  data() {
    return {
      image: '',
    }
  },

  methods: {
    selectFile(file) {
      this.image = file;
    },

    // Axios
    async submit() {
      const formData = new FormData();
      formData.append('image', this.image)

      try {
        const { data } = await axios.post(
          'http://127.0.0.1:3000/product/create',
          formData,
          {
            headers: {
              "Content-Type": "multipart/form-data"
            },
          }
          )
          console.log(data);
      } catch (err) {
        console.log(err)
      }
    }
  }
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.submissionDiv {
  width: 30%;
  height: fit-content;
  text-align: center;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 5px;
  padding: 5px;
  border-radius: 5px;
  background-color: white;
}
</style>